.ranking-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100vw;
}

.ranking-title {
  background-color: var(--blue);
  box-shadow: 0 0 10px var(--gray2);
  color: white;
  font-size: 2rem;
  padding: 15px;
}

.ranking-btn {
  background-color: transparent;
  border: none;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  font-size: 1.2rem;
  height: 50px;
  margin: 15px;
  padding: 10px;
  position: absolute;
  width: 50px;
}

.ranking-btn:hover {
  color: rgba(255, 255, 255, 0.765);
}

.home-icon {
  height: 100%;
  width: auto;
}

.ranking-sect {
  height: fit-content;
  margin: 15px auto;
  max-width: 90%;
  min-height: 80vh;
  padding: 8px;
  width: 90vh;
}

.ranking-card {
  align-items: center;
  border-radius: 50px;
  box-shadow: 0 0 15px var(--gray2);
  display: flex;
  font-size: 1.5rem;
  height: 100px;
  justify-content: space-between;
  margin: 15px 0;
  padding: 0 20px;
}

.ranking-card img {
  border-radius: 50%;
  height: 95%;
  margin-left: 15px;
  object-fit: cover;
  width: 100px;
}

.ranking-name {
  font-weight: 700;
}

@media screen and ( max-width : 750px ) {

  .ranking-card {
    font-size: 1.2rem;
    height: 80px;
  }

  .ranking-card img {
    height: auto;
    margin-left: 5px;
    width: 50px;
  }
}
